import { PlainTextList } from "@tanyoknits/shared";
import { RaglanPattern } from "../../patternMakers/raglan";
import { overview } from "./pattern";
import { measurements, schemaBySize, sizes } from "./sizes";
import { RaglanMeasurement } from "../../utils/sizes";
import { motifABySize, motifBBySize } from "./stitches";
import YokeInstruction from "./YokeInstruction";
import BodyInstruction from "./BodyInstruction";
import SleeveInstruction from "./SleeveInstruction";
import { MotifBySizeItem } from "../../utils/types";
import PageContentWrapper from "../../layout/PageContentWrapper";

export interface InstructionProps {
  schema: RaglanPattern;
  measurement: RaglanMeasurement;
}

interface Props {
  menu: number;
  size: number;
}

export default function Instruction({ menu, size }: Props) {
  let content = null;
  if (menu === 0) {
    content = <PlainTextList list={overview} />;
  } else if (menu === 1) {
    const motifB = motifBBySize.filter((m: MotifBySizeItem) =>
      m.sizes.includes(sizes[size])
    )[0].motif;

    content = (
      <YokeInstruction
        schema={schemaBySize[size]}
        measurement={measurements[size] as RaglanMeasurement}
        centerChartStsCount={motifB?.stsCount}
      />
    );
  } else if (menu === 2) {
    content = (
      <BodyInstruction
        schema={schemaBySize[size]}
        measurement={measurements[size] as RaglanMeasurement}
      />
    );
  } else {
    const motifA = motifABySize.filter((m: MotifBySizeItem) =>
      m.sizes.includes(sizes[size])
    )[0].motif;

    content = (
      <SleeveInstruction
        schema={schemaBySize[size]}
        measurement={measurements[size] as RaglanMeasurement}
        centerChart={{ sts: motifA?.stsCount, row: motifA?.chart.length }}
      />
    );
  }
  return <PageContentWrapper>{content}</PageContentWrapper>;
}
